import currencyUI from "./currency";
import favourites from "../store/favourites";
var TicketsUI = /** @class */ (function () {
    function TicketsUI(currency, favoutites) {
        this.container = document.querySelector('.tickets-sections .row');
        this.getCurrencySymbol = currency.getCurrencySymbol.bind(currencyUI);
        this.addFavourite = favourites.addToFavourites.bind(favourites);
    }
    TicketsUI.prototype.renderTickets = function (tickets) {
        var _this = this;
        this.clearContainer();
        if (!tickets.length) {
            this.showEmptyMsg();
            return;
        }
        var fragment = '';
        tickets.forEach(function (ticket, ind) {
            var template = TicketsUI.ticketTemplate(ticket, ind, _this.getCurrencySymbol());
            fragment += template;
        });
        this.container.insertAdjacentHTML('afterbegin', fragment);
        var toFavoriteBtns = document.querySelectorAll('.ticket-card .add-favourite-btn');
        toFavoriteBtns.forEach(function (btn) {
            btn.addEventListener('click', function (e) {
                if (e.target instanceof HTMLElement) {
                    _this.addFavourite(tickets[Number(e.target.dataset.ticketind)]);
                }
            });
        });
    };
    TicketsUI.prototype.clearContainer = function () {
        this.container.innerHTML = '';
    };
    TicketsUI.prototype.showEmptyMsg = function () {
        var template = TicketsUI.emptyMsgTemplate();
        this.container.insertAdjacentHTML('afterbegin', template);
    };
    TicketsUI.emptyMsgTemplate = function () {
        return "\n    <div class=\"tickets-empty-res-msg\">\n      \u041F\u043E \u0432\u0430\u0448\u0435\u043C\u0443 \u0437\u0430\u043F\u0440\u043E\u0441\u0443 \u0431\u0438\u043B\u0435\u0442\u043E\u0432 \u043D\u0435 \u043D\u0430\u0439\u0434\u0435\u043D\u043E.\n    </div>";
    };
    TicketsUI.ticketTemplate = function (ticket, ticketInd, symbol) {
        var airline_logo = ticket.airline_logo, airline_name = ticket.airline_name, origin_name = ticket.origin_name, destination_name = ticket.destination_name, departure_at_formated = ticket.departure_at_formated, price = ticket.price, transfers = ticket.transfers, flight_number = ticket.flight_number;
        return "\n    <div class=\"col s12 m6\">\n      <div class=\"card ticket-card\">\n        <div class=\"ticket-airline d-flex a-items-center\">\n          <img\n            src=\"".concat(airline_logo, "\"\n            class=\"ticket-airline-img\"\n          />\n          <span class=\"ticket-airline-name\"\n            >").concat(airline_name, "</span\n          >\n        </div>\n        <div class=\"ticket-destination d-flex a-items-center\">\n          <div class=\"d-flex a-items-center mr-auto\">\n            <span class=\"ticket-city\">").concat(origin_name, " </span>\n            <i class=\"medium material-icons\">flight_takeoff</i>\n          </div>\n          <div class=\"d-flex a-items-center\">\n            <i class=\"medium material-icons\">flight_land</i>\n            <span class=\"ticket-city\">").concat(destination_name, "</span>\n          </div>\n        </div>\n        <div class=\"ticket-time-price d-flex a-items-center\">\n          <span class=\"ticket-time-departure\">").concat(departure_at_formated, "</span>\n          <span class=\"ticket-price ml-auto\">").concat(symbol).concat(price, "</span>\n        </div>\n        <div class=\"ticket-additional-info\">\n          <span class=\"ticket-transfers\">\u041F\u0435\u0440\u0435\u0441\u0430\u0434\u043E\u043A: ").concat(transfers, "</span>\n          <span class=\"ticket-flight-number\">\u041D\u043E\u043C\u0435\u0440 \u0440\u0435\u0439\u0441\u0430: ").concat(flight_number, "</span>\n        </div>\n        <a\n          class=\"waves-effect waves-light btn pink accent-4 add-favourite-btn\"\n          data-ticketind=\"").concat(ticketInd, "\"\n        >Add to favourite</a>\n      </div>\n    </div>\n    ");
    };
    return TicketsUI;
}());
var ticketsUI = new TicketsUI(currencyUI, favourites);
export default ticketsUI;
