import currencyUI from "./currency";
import favourites from "../store/favourites";
var FavouritesUI = /** @class */ (function () {
    function FavouritesUI(currency, favoutites) {
        this.dropdown = document.querySelector('#dropdown1');
        this.getCurrencySymbol = currency.getCurrencySymbol.bind(currencyUI);
        this.favouritesList = favourites.favouritesList;
        this.deleteFavourite = favourites.deleteFavourite;
    }
    FavouritesUI.prototype.renderTickets = function (btn) {
        var _this = this;
        this.clearContainer();
        if (!this.favouritesList.length) {
            this.showEmptyMsg();
            var instance = M.Dropdown.getInstance(btn);
            instance.close();
            instance.open();
            return;
        }
        var fragment = '';
        this.favouritesList.forEach(function (favourite, ind) {
            var template = FavouritesUI.ticketTemplate(favourite, ind, _this.getCurrencySymbol());
            fragment += template;
        });
        this.dropdown.insertAdjacentHTML('afterbegin', fragment);
        var deleteFavouriteBtns = document.querySelectorAll('.favorite-item-info .delete-favorite');
        deleteFavouriteBtns.forEach(function (btn) {
            btn.addEventListener('click', function (e) {
                if (e.target instanceof HTMLElement) {
                    _this.deleteFavourite(Number(e.target.dataset.favouriteind));
                }
            });
        });
    };
    FavouritesUI.prototype.clearContainer = function () {
        this.dropdown.innerHTML = '';
    };
    FavouritesUI.prototype.showEmptyMsg = function () {
        var template = FavouritesUI.emptyMsgTemplate();
        this.dropdown.insertAdjacentHTML('beforeend', template);
    };
    FavouritesUI.emptyMsgTemplate = function () {
        return "\n    <div class=\"favorite-item favourite-empty-res-msg\">\n      \u0412\u044B \u043D\u0435 \u0434\u043E\u0431\u0430\u0432\u0438\u043B\u0438 \u0435\u0449\u0435 \u043D\u0438 \u043E\u0434\u043D\u043E\u0433\u043E \u0431\u0438\u043B\u0435\u0442\u0430 \u0432 \u0438\u0437\u0431\u0440\u0430\u043D\u043D\u043E\u0435\n    </div>";
    };
    FavouritesUI.ticketTemplate = function (ticket, ticketInd, symbol) {
        var airline_logo = ticket.airline_logo, origin_name = ticket.origin_name, destination_name = ticket.destination_name, departure_at_formated = ticket.departure_at_formated, price = ticket.price, transfers = ticket.transfers, flight_number = ticket.flight_number;
        return "\n    <div class=\"favorite-item  d-flex a-items-start\">\n      <img\n        src=\"".concat(airline_logo, "\"\n        class=\"favorite-item-airline-img\"\n      />\n      <div class=\"favorite-item-info d-flex flex-column\">\n        <div\n          class=\"favorite-item-destination d-flex a-items-center\"\n        >\n          <div class=\"d-flex a-items-center mr-auto\">\n            <span class=\"favorite-item-city\">").concat(origin_name, " </span>\n            <i class=\"medium material-icons\">flight_takeoff</i>\n          </div>\n          <div class=\"d-flex a-items-center\">\n            <i class=\"medium material-icons\">flight_land</i>\n            <span class=\"favorite-item-city\">").concat(destination_name, "</span>\n          </div>\n        </div>\n        <div class=\"ticket-time-price d-flex a-items-center\">\n          <span class=\"ticket-time-departure\">").concat(departure_at_formated, "</span>\n          <span class=\"ticket-price ml-auto\">").concat(symbol).concat(price, "</span>\n        </div>\n        <div class=\"ticket-additional-info\">\n          <span class=\"ticket-transfers\">\u041F\u0435\u0440\u0435\u0441\u0430\u0434\u043E\u043A: ").concat(transfers, "</span>\n          <span class=\"ticket-flight-number\">\u041D\u043E\u043C\u0435\u0440 \u0440\u0435\u0439\u0441\u0430: ").concat(flight_number, "</span>\n        </div>\n        <a\n          class=\"waves-effect waves-light btn-small pink darken-3 delete-favorite ml-auto\"\n          data-favouriteind=\"").concat(ticketInd, "\"\n          >Delete</a\n        >\n      </div>\n    </div>\n    ");
    };
    return FavouritesUI;
}());
var favouritesUI = new FavouritesUI(currencyUI, favourites);
export default favouritesUI;
