import 'materialize-css/dist/css/materialize.min.css';
import 'materialize-css/dist/js/materialize.min.js';
var dropdown = document.querySelector('.dropdown-trigger');
M.Dropdown.init(dropdown);
var select = document.querySelectorAll('select');
M.FormSelect.init(select);
export var getSelectInstance = function (elem) { return M.FormSelect.getInstance(elem); };
var autocomplete = document.querySelectorAll('.autocomplete');
M.Autocomplete.init(autocomplete, {
    data: {}
});
export var getAutocompleteInstance = function (elem) { return M.Autocomplete.getInstance(elem); };
var datePicker = document.querySelectorAll('.datepicker');
M.Datepicker.init(datePicker, {
    showClearBtn: true,
    format: 'yyyy-mm'
});
export var getDatePickerInstance = function (elem) { return M.Datepicker.getInstance(elem); };
